.ChoixOutput{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0% 5% 2% 5%;

}

.carte-liste-output{
    display: flex;
    flex-direction: row;
    gap: 5%;
    margin-top: 2%;
    margin-bottom: 2%;
}

.carte-liste-output .card{
    display: flex;
    flex-direction: column;
    align-items:  center;
    min-height: 500px;
    padding: 3.5rem;

}

.carte-liste-output .txt-btn{
    display: flex;
    flex-direction: row;
    align-items:center;
    height: 20%;
}



.carte-liste-output h1{
    padding-bottom: 10px;
}



.carte-liste-output Button {

    font-size: 18px;
    margin-top: 15%;

}

